import { GraphCore } from '@/graph-core/graph-core';
import { Options } from "@antv/x6/lib/graph/options";
import { Cell, CellView, Node, Rectangle, Graph, Model } from '@antv/x6';

export const config = (dom: HTMLElement, minimap?: HTMLElement): Partial<Options.Manual> => {
    return {
        container: dom,
        snapline: false,
        background: {
            color: '#f5f5f5',
        },
        grid: {
            visible: false,
            size: 1,
        },
        scroller: {
            enabled: true,
            pageVisible: true,
            // pageBreak: true,
            pannable: true,
        },
        mousewheel: {
            enabled: true,
            modifiers: ['ctrl', 'meta'],
            minScale: 0.2,
            maxScale: 2,
        },
        minimap: {
            enabled: true,
            container: minimap,
            graphOptions: {
                async: true,
                createCellView(cell) {
                  // 
                  if (cell.isEdge()) {
                    return null
                  }
                },
              }          
        },
        rotating: {
            enabled: (node: Node<Node.Properties>) => {
                const extra: string[] = ['exhaust', 'exhaust-vertical','exhaust-B','exhaust-B-vertical', 'MonitoringPoint']
                if (extra.includes(node.shape)) {
                    return false
                }
                return true
            },
            grid: 1
        },
        resizing: {
            enabled: (node: Node<Node.Properties>) => {
                let resize = true
                const nonResizable: string[] = ['board', 'CenterBoard', 'domain', 'HeatPipe', 'MonitoringPoint']
                if (nonResizable.includes(node.shape)) {
                    resize = false
                }
                return resize
            },
            minHeight: (node: Node<Node.Properties>) => {
                const extra: string[] = ['exhaust', 'exhaust-B']
                if (extra.includes(node.shape)) {
                    return node.getSize().height 
                }
                return undefined
            },
            maxHeight: (node: Node<Node.Properties>) => {
                const extra: string[] = ['exhaust', 'exhaust-B']
                if (extra.includes(node.shape)) {
                    return node.getSize().height 
                }
                return undefined
            },
            minWidth: (node: Node<Node.Properties>) => {
                const extra: string[] = ['exhaust-vertical', 'exhaust-B-vertical']
                if (extra.includes(node.shape)) {
                    return node.getSize().width 
                }
                return undefined
            },
            maxWidth: (node: Node<Node.Properties>) => {
                const extra: string[] = ['exhaust-vertical', 'exhaust-B-vertical']
                if (extra.includes(node.shape)) {
                    return node.getSize().width 
                }
                return undefined
            },
        },
        // /
        selecting: {
            enabled: true,
            filter: (cell: Cell) => {
                let select = true
                const unselectable: string[] = ['board', 'CenterBoard', 'domain', 'HeatPipe']
                if (unselectable.includes(cell.shape)) {
                    select = false
                }
                return select
            }
        },
        // 
        interacting: (cellView: CellView) => {
            // 
            let nodeMovable = true
            const unmovable: string[] = ['board', 'CenterBoard', 'domain', 'HeatPipe']
            if (unmovable.includes(cellView.cell.shape)) {
                nodeMovable = false
            }
            return { nodeMovable }
        },
        // 
        translating: {
            restrict: (cellView: CellView): Rectangle.RectangleLike => {

                if(cellView.cell.data?.lockPosition){
                    return cellView.cell.getBBox();
                }

                if (cellView.cell.parent) {
                    return cellView.cell.parent!.getBBox()
                }
                
                // 
                const extra: string[] = ['exhaust', 'exhaust-vertical','exhaust-B','exhaust-B-vertical']
                if (extra.includes(cellView.cell.shape)) {
                    const area = GraphCore.layoutBoard.getBBox()
                    const box = cellView.cell.getBBox()
                    if (!cellView.cell.shape.includes('vertical')) {
                        return {
                            x: area.left,
                            y: box.top,
                            height: box.height,
                            width: area.width
                        }
                    } else {
                        return {
                            x: box.left,
                            y: area.top,
                            height: area.height,
                            width: box.width
                        }
                    }
                }

                // console.log(GraphCore.graph.getContentArea())
                return GraphCore.graph.getGraphArea();
            }
        },
        keyboard: {
            enabled: true,
            global: true,
        }
    }
}

