import { GraphCore, OptimizationStage } from '@/graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import { OptimizationForm, Plans } from './class/optimization-form';
import { Subscription } from 'rxjs/internal/Subscription';
import PlanOneStepOne from './plan-1-step-1/plan-1-step-1.vue';
import PlanOneStepTwo from './plan-1-step-2/plan-1-step-2.vue';
@Component({
  components: {
    PlanOneStepOne,
    PlanOneStepTwo
  },
})
export default class Optimization extends Vue {
  form: any = new OptimizationForm
  plans = Plans
  rules = {
    plan: [{ required: true, message: '', trigger: 'change' }],
  }

  optimizationStage: OptimizationStage = GraphCore.optimizationStage$.getValue();
  
  // 
  subscribes: Subscription[] = []

  mounted() {
    // 
    this.subscribes.push(GraphCore.optimizationStage$.subscribe((optimizationStage) => this.optimizationStage = optimizationStage))
  }


  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }
  
  onSubmit() {
    (this.$refs.initForm as any).validate((valid: any) => {
      if (valid) {
        this.$emit('submit')
        console.log(this.form)
        GraphCore.optimizationStage$.next({plan: this.form.plan, step: 1})
      } else {
        console.log('error submit!!');
      }
    });
  }
}