import { GraphCore } from './../../../../graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import { Subscription } from 'rxjs/internal/Subscription';


@Component({
  components: {

  },
})
export default class CreateYml extends Vue {

  form: any = {
    task: 'continuous',
    ndim: 2,
    length: 0.1,
    nx: 200,
    u_D: 298,
    bcs: [],
    units: [],
    powers: [],
    geometry: [],
    power_distribution: [],
    angles: [],
    positions_type: 'grid',
    positions: [],
    gaussian_param: 1,
    monitoring_sampling: [
        ['file', 'config/Example0.mat']
    ],
    data_dir: 'example_dataset',
    file_format: 'mat',
    prefix: 'Example',
    sample_n: 2,
    special: 'n',
    special_num: 0,
    fem_degree: 1,
    method: 'fenics'

  }


  monitoring_sampling_type: string = 'file'
  monitoring_sampling_params: number[] = [0, 0]
  monitoring_sampling_filePath: string = 'config/Example0.mat'
  monitoring_sampling_custom: string = ''


  // 
  subscribes: Subscription[] = []

  mounted() {
    this.initSubscribes()
  }

  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.nx$.subscribe((nx: number) => {
      this.form.nx = nx
    }))
  }

  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

  monitoring_sampling_changed() {
    switch (this.monitoring_sampling_type) {
      case 'random':
        this.form.monitoring_sampling = [['random', this.monitoring_sampling_params[0]]]
        break;
      case 'center':
        this.form.monitoring_sampling = [['center', this.monitoring_sampling_params[0], this.monitoring_sampling_params[1]]]
        break;
      case 'uniform':
        this.form.monitoring_sampling = [['uniform', this.monitoring_sampling_params[0], this.monitoring_sampling_params[1]]]
        break;
      case 'file':
        this.form.monitoring_sampling = [['file', this.monitoring_sampling_filePath]]
        break;
      default:
        this.form.monitoring_sampling = [[this.monitoring_sampling_type, this.monitoring_sampling_custom]]
        break;
    }
  }

  createYml() {
    GraphCore.createYml(this.form)
  }


}