import { NodeData } from './../graph-nodes-vue';
import { GraphCore, PropKeyValue, UpdateNodePropModal } from '@/graph-core/graph-core';
import { VueNodeConfig } from '../VueNodeConfig';
import { Component, Prop, Vue } from 'vue-property-decorator';
import { Node, Cell } from '@antv/x6'
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  components: {

  },
})
export default class VueShapeNode extends Vue {
  
  @Prop() id!: string
  
  node: Node | null = null
  position: any = {}
  size: any = {}
  data: NodeData = {} as NodeData

  // 
  subscribes: Subscription[] = []

  mounted() {
    console.log(this.id)
    if (this.id) {
      this.node = GraphCore.graph.getCellById(this.id) as Node
      this.position = this.node.getPosition()
      this.size = this.node.getSize()
      this.data = this.node.getData()
      console.log(this.node)
      this.initSubscribes()
    }
  }

  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.updateNodeProp$.subscribe(this.onUpdateNodeProp))
    this.subscribes.push(GraphCore.nodeMoved$.subscribe(this.onNodeMoved));
    this.subscribes.push(GraphCore.nodeResized$.subscribe(this.onNodeResized))
  }

  // 
  destroyed() {
    console.log(`component ${this.id} destroyed`)
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

  // [] 
  onUpdateNodeProp({ id, data }: UpdateNodePropModal) {
    if (id && id === this.node?.id) {
      // 
      data.forEach((item: PropKeyValue) => {
        this.node?.prop(item.key, item.value)
      })
      this.data = this.node.getData()
      // // * X6vue
      // this.$forceUpdate()
    }
  }

  // [] 
  onNodeMoved(node: Node|null) {
    if (node && node.id === this.node!.id) {
      this.position = this.node!.position()
    }
  }

  // [] 
  onNodeResized(node: Node|null){
    if (node && node.id === this.node!.id) {
      this.size = this.node!.size()
    }
  }

  // 
  onContextmenu(event: MouseEvent) {
    const { pageX, pageY } = event
    GraphCore.contextMenuData$.next({
      position: { left: pageX + 'px', top: pageY + 'px' },
      node: this.node as Node
    })
  }

  

}