import { GraphCore } from '@/graph-core/graph-core';
import { Node, Graph } from '@antv/x6';
import { cloneDeep } from '@antv/x6/lib/util/object/object';
import { Component, Prop, Vue, Watch } from 'vue-property-decorator';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  components: {

  },
})
export default class NodeProperty extends Vue {
  @Prop() node!: Node;

  data: any = {}
  form: any = {}
  size = { width: 0, height: 0 }
  position = { x: 0, y: 0 };
  angle: number = 0
  apexOffset = 0

  // 
  subscribes: Subscription[] = []
  
  mounted() {
    this.onNodeChange()
    this.initSubscribes()
    
  }

  @Watch('node')
  onNodeChange() {
    console.log(this.node)
    this.data = this.node.getData() || {}
    this.form = cloneDeep(this.data)
    this.angle = this.node.getAngle()
    this.onNodeMove();
    this.onNodeResize();

    if (this.node.shape === 'triangle') {
      console.log(this.node.getProp().points);
      this.apexOffset = parseFloat((this.node as any).points.split(',')[0])
    } else {
      this.apexOffset = 0
    }
  }

  //
  onNodeMove() {
    if (this.node.shape !== 'MonitoringPoint') {
      this.position = this.node.position();
    } else {
      this.position = this.node.getBBox().center;
    }
    
  }
  
 //
  onNodeResize(){
    this.size=this.node.size();
  }

  onNodeRotate() {
    this.angle = this.node.getAngle()
  }


  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.nodeMove$.subscribe(this.onNodeMoving));
    this.subscribes.push(GraphCore.nodeResize$.subscribe(this.onNodeResized))
    this.subscribes.push(GraphCore.nodeRotated$.subscribe(this.onNodeRotate))
  }

  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

  // 
  onNodeDataChange() {
    // GraphCore.updateNodeProp$.next({
    //   id: this.node.id,
    //   data: [{ key: 'data', value: this.form }]
    // })

    this.node.prop('data', this.form)
  }

  // 
  onNodeSizeChange() {
    if (this.size.width > 0 && this.size.height > 0) {
      // GraphCore.updateNodeProp$.next({
      //   id: this.node.id,
      //   data: [
      //     { key: 'size/width', value: this.size.width },
      //     { key: 'size/height', value: this.size.height }
      //   ]
      // })
      this.node.resize(this.size.width,this.size.height);
    }
  }
  //
  onSafetyBoxChange(){
    if(this.node.hasTool("boundary")){
      this.node.removeTool("boundary");
      this.node.addTools({
        name:"boundary",
        args:{
          padding:this.data.minimum_margin,
          attrs: {
            //fill: "#66DE93",
            stroke: "#D83A56",
            strokeWidth: 2,
            fillOpacity:0.2
          }   
        }
      });
    }
  }

  //
  onNodePositionChange(){
    this.node.position(this.position.x,this.position.y);
  }

  onNodeCenterPositionChange() {
    this.node.position(this.position.x - 5,this.position.y - 5);
  }
  
  //
  onNodeMoving(node: Node|null) {
    if (node && node.id === this.node.id) {
      this.onNodeMove();
    }
  }

  //
  onNodeResized(node: Node|null){
    if (node && node.id === this.node.id) {
      this.onNodeResize();
    }
  }

  // 
  onNodeFillChange() {
    console.log(this.form.fill)
    this.node.prop('attrs/body/fill', this.form.fill)
  }

  // 
  onNodeStrokeChange() {
    console.log(this.form.stroke)
    this.node.prop('attrs/body/stroke', this.form.stroke)
  }

  // 
  onApexOffsetChange() {
    (this.node as any).points = (this.apexOffset||0).toString() +',0 0,1 1,1'
  }

  // 
  onAngleChange() {
    if (this.angle < -360 || this.angle > 360) {
      return
    }
    if (this.angle || this.angle === 0) {
      this.node.rotate(parseFloat(this.angle.toString()), { absolute: true })
    }
    
  }

}