import { Component, Prop, Vue } from 'vue-property-decorator'
import { Addon, Node } from '@antv/x6'
import { GraphCore } from '@/graph-core/graph-core'
import { BehaviorSubject, Subject } from 'rxjs'
import ComponentNode  from '@/views/layout/toolbar/add-component/component-node/component-node.vue'
import components from './mock-compoents'
import VueShapeNode from '@/graph-core/graph-nodes/vue-shape-node/vue-shape-node.vue';
import { v4 as uuidv4 } from 'uuid'

@Component({
  components: {
    ComponentNode
  },
})
export default class AddComponent extends Vue {
  dragOut: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false) // 

  components: any[] = components  // 
  draggingComp: BehaviorSubject<any> = new BehaviorSubject({});  // 

  dnd = new Addon.Dnd({
    target: GraphCore.graph,
    // *  getDropNode -> validateNode
    // 
    getDropNode: (node: Node) => {
      const n: any = node.clone()
      //  + 1
      const index = GraphCore.lastId$.getValue() + 1
      const id = uuidv4()
      // dataid
      const data = {
        ...n.data,
        index: index.toString(),
        id,
      }
      // *vue-shape, dataVueShapeNode
      n.id = id
      n.setData(data)
      // 
      return n
    },
    // 
    validateNode: (droppingNode: Node<Node.Properties>, options: Addon.Dnd.ValidateNodeOptions) => {
      window.removeEventListener('mousemove', this.onDragOut)
      const valid: boolean = this.dragOut.getValue()
    
      // 
      if (valid) {
        const extra: string[] = ['exhaust','exhaust-B']
        if (extra.includes(droppingNode.shape)) {
          const position = GraphCore.checkPointAroundBoard(droppingNode.getBBox().getCenter())
          if (position==='') {
            this.$message.error('')
          } else {
            GraphCore.addExhaust(position, droppingNode.getBBox(), droppingNode.shape)
          }
          return false
        }
  
        // 
        const nodes = GraphCore.graph.getNodesFromPoint(droppingNode.getPosition())
        const board = nodes.find((node: Node) => node.shape === 'board')
        if (!board) {
          this.$message.error('')
          return false
        } else {
          // 
          // const intersects = GraphCore.graph.getNodesInArea(droppingNode.getBBox())
          // if (intersects.some((node: Node) => node.shape === 'vue-shape')) {
          //   this.$message.error('')
          //   return false
          // }
            
          
          // console.log('pass')
          droppingNode.zIndex = 2
          board.addChild(droppingNode)
          // console.log(droppingNode)
          GraphCore.lastId$.next(parseInt(droppingNode.data.index))
          return true
        }
      }
      return valid
    },
    
  })

  

  onDragOut(e: MouseEvent) {
    if (e.clientX > 256) {
      this.dragOut.next(true)
    }
  }

  drag(e: Event, comp: any) {
    this.draggingComp.next(comp)
    this.dragOut.next(false)

    window.addEventListener('mousemove', this.onDragOut)
    this.dnd.start(GraphCore.graph.createNode(comp), e)
  }


}