import { Component, Prop, Vue } from 'vue-property-decorator';
import { Subscription } from 'rxjs/internal/Subscription';
import { GraphCore, OptimizationStage } from '@/graph-core/graph-core';
import { OptimizationForm, Plans } from '../class/optimization-form';

@Component({
  components: {

  },
})
export default class PlanOneStepOne extends Vue {

  @Prop() optimizationStage!: OptimizationStage

  form: any = {v: 'kano'}
  plans = Plans
  rules = {
    v: [{ required: true, message: '', trigger: 'change' }],
  }

  
  // 
  subscribes: Subscription[] = []

  mounted() {
    // 
    // this.subscribes.push(GraphCore.optimizationStage$.subscribe())
  }


  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

  // 
  onSubmit() {
    (this.$refs.initForm as any).validate((valid: any) => {
      if (valid) {
        this.$emit('submit')
        console.log(this.form)
      } else {
        console.log('error submit!!');
      }
    });
  }


  // 
  next() {
    GraphCore.optimizationStage$.next({plan: this.optimizationStage.plan, step: 2})
  }

  // 
  back() {
    GraphCore.optimizationStage$.next({plan: null, step: 1})
  }

}