import { ContextMenuData, GraphCore } from '@/graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import { Subscription } from 'rxjs/internal/Subscription';
import { Node } from '@antv/x6';

@Component({
  components: {

  },
})
export default class ContextMenu extends Vue {
  show: boolean = false
  contextMenuPosition = { left: '0px', top: '0px' }

  node: Node|null = null

  // 
  subscribes: Subscription[] = []

  mounted() {
    this.initSubscribes()
  }

  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.contextMenuData$.subscribe((contextMenuData: ContextMenuData) => {
      this.show = contextMenuData.node !== null
      this.node = contextMenuData.node
      this.contextMenuPosition = contextMenuData.position
    }))
  }

  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

  // 
  deleteNode() {
    GraphCore.graph.removeNode(this.node!.id)
  }



}