import { GraphCore } from '@/graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import { GlobalConfig } from './class/global-config';
import { Subscription } from 'rxjs/internal/Subscription';

@Component({
  components: {

  },
})
export default class Settings extends Vue {
  form: GlobalConfig = new GlobalConfig
  rules = {
    nx: [{ required: true, message: '', trigger: 'change' }],
  }

  // 
  subscribes: Subscription[] = []

  mounted() {
    this.initSubscribes()
  }

  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.nx$.subscribe((nx: number) => {
      this.form.nx = nx
    }))
  }

  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }
}