import { OptimizationStage, GraphCore } from '@/graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import AddComponent from './add-component/add-component.vue';
import Initialization from './initialization/initialization.vue';
import Optimization from './optimization/optimization.vue';
import CreateYml from './create-yml/create-yml.vue';
import Settings from './settings/settings.vue'

@Component({
  components: {
    AddComponent,
    Initialization,
    Optimization,
    CreateYml,
    Settings,
  },
})
export default class ToolBar extends Vue {
  addCompDrawer: boolean = false; // drawer
  InitializationDrawer: boolean = false;  // drawer
  settingDrawer: boolean = false;  // drawer
  optimizationDrawer: boolean = false; // drawer

  createYmlModal: boolean = false;
  
  // yml
  createYml() {
    // GraphCore.createYml()
    (this.$refs.createYmlComp as any).createYml()
  }

  // 
  createMonitoringPoint() {
    GraphCore.createMonitoringPointFile()
  }

}