import { Component, Vue } from 'vue-property-decorator';
import { Subscription } from 'rxjs/internal/Subscription';
import { GraphCore } from '@/graph-core/graph-core';

@Component({
  components: {

  },
})
export default class StatusManager extends Vue {

  useMonitoringPoint: boolean = false

  // 
  subscribes: Subscription[] = []

  mounted() {
    this.initSubscribes()
  }

  //  
  initSubscribes() {
    this.subscribes.push(GraphCore.useMonitoringPoint$.subscribe((useMonitoringPoint: boolean) => {
      this.useMonitoringPoint = useMonitoringPoint
    }))
  }

  // 
  destroyed() {
    this.subscribes.forEach((subscription: Subscription) => subscription.unsubscribe())
  }

}