import { GraphCore } from '@/graph-core/graph-core';
import { Component, Vue } from 'vue-property-decorator';
import { InitForm } from './class/init-form';


@Component({
  components: {

  },
})
export default class Initialization extends Vue {
  form: InitForm = new InitForm
  rules = {
    layoutWidth: [{ required: true, message: '', trigger: 'change' }],
    layoutHeight: [{ required: true, message: '', trigger: 'change' }],
  }

  mounted() {
    
  }

  onSubmit() {
    (this.$refs.initForm as any).validate((valid: any) => {
      if (valid) {
        this.$emit('submit')
        GraphCore.initializationBoard(this.form)
      } else {
        console.log('error submit!!');
      }
    });
  }
}